import React from "react";
import { Card, CardImg, CardText, CardBody,
  CardTitle, CardSubtitle, Button, Collapse, CardHeader } from 'reactstrap';
import descripcion_img_1 from '../img/1_imagen_original.jpg';
import descripcion_img_2 from '../img/2_seleccion_area.jpg';
import descripcion_img_3 from '../img/3_result_final.png';
import FaAngellist from 'react-icons/lib/fa/angellist';
import FaQuestion from 'react-icons/lib/fa/question';
import FaCode from 'react-icons/lib/fa/code';
import FaExternalLink from 'react-icons/lib/fa/external-link';
import FaAngleDown from 'react-icons/lib/fa/angle-down';


class Documentacion extends React.Component {
	constructor(props) {
		super(props);
		this.toggle = this.toggle.bind(this);
		this.state = { collapse: 0, cards: [1, 2, 3, 4, 5] };
	}

	toggle(e) {
	  let event = e.target.dataset.event;
	  this.setState({ collapse: this.state.collapse === Number(event) ? 0 : Number(event) });
	}

  render() {
  	const {cards, collapse} = this.state;
    return (
      <div>
      	<div>
	          <Card>
	            <CardHeader onClick={this.toggle} data-event={1}><FaAngellist size={40}/>Motivacin<FaAngleDown className="float-right" size={40}/></CardHeader>
                <Collapse isOpen={collapse === 1}>

	            <CardBody>
					<p>Este proyecto plantea como iniciativa el desarrollo de una aplicacin multimedia que permita la correccin dinmica de la perspectiva de las imgenes captadas por una webcam. Como ncleo medular se busca conseguir la correccin de la perspectiva de una imagen.</p>
					<p>El proyecto otorgar la posibilidad a los usuarios de interactuar con la perspectiva de un vdeo, buscando tanto aprender como dejar reflejado aquello se puede conseguir al hacer un empleo combinado de una serie de diversos conocimientos matemticos  y tecnolgicos.</p>
					<p>Las principales utilidades que hemos encontrado entre otras son:</p>
					<ul>
						<li>Correccin de perspectiva de vdeo en las conferencias.</li>
						<li>Correccin de perspectiva en escaneo de documentos (tickets).</li>
						<li>Visualizacin de un objeto con la perspectiva correcta: por ejemplo, de una Pizarra girada, ponerla de frente.</li>
						<li>Modificacin de la perspectiva de la imagen en funcin de los deseos del usuario.</li>
						<li>Medicin de distancias en imgenes tomadas por cmaras de trfico [4].</li>
						<li>Contabilizacin de vehculos en un determinado tramo de carretera [5].</li>
					</ul>
					<p>Los resultados que se pueden conseguir son muy potentes. Adems, tenemos la intencin de conseguir ms y nuevos resultados de manera incremental con el objetivo de investigar y seguir probando en combinacin otras herramientas.</p>
	            
	            </CardBody>
	            	        </Collapse>

	          </Card>
        </div>
        <div>
	          <Card>
	            <CardHeader onClick={this.toggle} data-event={2}><FaQuestion size={40}/>Descripcin<FaAngleDown className="float-right" size={40}/></CardHeader>
            	<Collapse isOpen={collapse === 2}>
	            <CardBody>
					<p>El elemento de perspectiva es utilizado para hacer referencia a la dimensin de los objetos en una imagen y a la relacin espacial que existe entre ellos respecto a un punto de vista. Jugando con la perspectiva se consiguen efectos sorprendentes aplicando distintas transformaciones.</p>
	            	<p>La transformacin o distorsin de la perspectiva de una imagen es  la transformacin propiamente dicha de un objeto y el rea que lo rodea. Esto provoca que el aspecto del conjunto sea distinto respecto a cmo se vera presentado una longitud focal normal [6]. Por lo tanto, juega un papel muy importante el ngulo de visin. Con este proyecto se quiere jugar con el ngulo de visin simulando que una fotografa ha sido, por ejemplo, tomada desde otro punto de vista, como se visualiza en el siguiente ejemplo.</p>
	            	<p>A continuacin podemos observar cmo transformamos la perspectiva de una fotografa de la facultad para poder ver las letras de frente. De la misma manera vemos como otras zonas de la foto quedan deformadas, como es el caso de las ventanas.</p>
	            	<img src={descripcion_img_1} width="450" height="350" />
	            	<img src={descripcion_img_2} width="450" height="350" />
	            	<img src={descripcion_img_3} width="450" height="350" />
	            </CardBody>
	            </Collapse>
	          </Card>
        </div>
        <div>
	          <Card>
	            <CardHeader onClick={this.toggle} data-event={3}><FaCode size={40}/>Tecnologas empleadas<FaAngleDown className="float-right" size={40}/></CardHeader>
            	<Collapse isOpen={collapse === 3}>
		            <CardBody>
						<p>La aplicacin se va a desarrollar fundamentalmente con tecnologas web. Para recoger el flujo de vdeo de la webcam vamos a utilizar el estndar WebRTC, y para aplicar la transformacin de la perspectiva vamos a utilizar una transformacin CSS matrix3d tal y como se hace en [2] y [3]. Eso nos garantiza ms fluidez en la interaccin, ya que estaremos utilizando la tarjeta grfica para realizar los clculos para la transformacin de las imgenes.</p>
		            </CardBody>
	           	</Collapse>

	          </Card>
        </div>
        <div>
	          <Card>
	            <CardHeader onClick={this.toggle} data-event={4}><FaExternalLink size={40}/>Referencias<FaAngleDown className="float-right" size={40}/></CardHeader>
            	<Collapse isOpen={collapse === 4}>
		            <CardBody>
			            <ul>
							<li>[1]	P. N. Klein, Coding the Matrix: Linear Algebra through Applications to Computer Science, 1 edition. Newton, Mass.: Newtonian Press, 2013.</li>
							<li>[2]	Perspective Transforms in JavaScript. [Online]. Available: <a href="https://www.uncorkedstudios.com/blog/perspective-transforms-in-javascript/" rel="nofollow">https://www.uncorkedstudios.com/blog/perspective-transforms-in-javascript/</a>. [Accessed: 28-Feb-2018].</li>
							<li>[3]	Perspective Transformation. [Online]. Available: <a href="https://bl.ocks.org/mbostock/10571478" rel="nofollow">https://bl.ocks.org/mbostock/10571478</a>. [Accessed: 28-Feb-2018].</li>
							<li>[4]	 Cmaras de trafico: <a href="https://www.researchgate.net/figure/Figura-2-Ajuste-de-transformacion-de-perspectiva-y-obtencion-de-coordenadas-cartesianas-s_fig1_305755376" rel="nofollow">https://www.researchgate.net/figure/Figura-2-Ajuste-de-transformacion-de-perspectiva-y-obtencion-de-coordenadas-cartesianas-s_fig1_305755376</a>
							</li>
							<li>[5] 	Conteo de trfico de vehculos en intersecciones: <a href="http://utc.ices.cmu.edu/utc/tier-one-reports/Taylor_TSETFinalReport.pdf" rel="nofollow">http://utc.ices.cmu.edu/utc/tier-one-reports/Taylor_TSETFinalReport.pdf</a>
							</li>
							<li>[6]	Distorsin de la perspectiva: <a href="https://es.wikipedia.org/wiki/Distorsi%C3%B3n_de_la_perspectiva" rel="nofollow">https://es.wikipedia.org/wiki/Distorsi%C3%B3n_de_la_perspectiva</a>
							</li>
						</ul>
		            </CardBody>
	           	</Collapse>

	          </Card>
        </div>
      </div>
    );
  }
}

export default Documentacion;


